.navbar_collapse_show {
    height: 100vh;
    background-color: #054b62c8;
    /* background-color: #adff2f; */
    font-family: "Orbitron", sans-serif;
    border: 3px solid #58a1b9;
    box-shadow: 0px 0px 4px #65beda;
    width: 80%;
    color: #ffffff;
    z-index: 200;
    position: absolute;
    transition: all 0.5s;
    backdrop-filter: blur(12px);


}

.navbar_collapse_hide {
    background-color: #054b62c8;
    border: 3px solid #58a1b9;
    box-shadow: 0px 0px 4px #65beda;
    font-family: "Orbitron", sans-serif;
    color: #ffffff;
    height: 100vh;
    width: 80%;
    position: absolute;
    transform: translateX(-100%);
    transition: all 0.5s;
    backdrop-filter: blur(12px);

}

.nav {
    display: flex;
    flex-direction: column;
    font-size: 2rem;
    padding-left: 1rem;
    margin-top: 45%;
}

p {
    margin: 0px;
}

p:hover {
    color: #adff2f
}

.link {
    margin-bottom: 2rem;
    font-size: 1.5rem;
    color: #61dafb;
}

.close {
    height: 3rem;
}

.close_btn {
    background: none;
    border: none;
    color: #a8e7fa;
}

.close_btn:hover {
    color: #adff2f;

}