.img {
    width: 100%;
    height: 100vh;
    background: url("/public/leaderboard.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: fixed;
    top: 0%;
    left: 0%;
}

h1 {
    font-family: "Orbitron";
    font-style: normal;
    font-weight: 700;
    font-size: 60px;
    line-height: 75px;
    color: #ffffff;
    text-align: center;
    padding-top: 60px;
    letter-spacing: 0.3rem;
}

@media screen and (max-width: 550px) {
    h1 {
        font-size: 32px;
    }
}
